// Dark style for document elements
.dark {
  background: $DarkThemeBackgroundColor;
  color: $DarkThemeColorWhite;
}

  body.dark{
.header {
  box-shadow: 0px 0px 1px 0px #ffffff;
}

.header-library {
  box-shadow: 0px 0px 1px 0px #ffffff ;
}

.footer {
  background: #1c1c1c;
  color: $DarkThemeColorWhite ;
  box-shadow: 0px 0px 1px 0px #ffffff;

}

.scroll-top {
  background: transparent;
  color: $DarkThemeColorWhite ;
}

// End dark style for document elements

// Dark style for modal elements
.modal {
  background: $DarkThemeModalBackground ;
  border-color: rgba($color: #ffffff, $alpha: 0.3);
}

  .modalBackdrop {
  background-color: rgba(100, 100, 100, 0.1);
}

  .modal-close-btn {
  color: $DarkThemeColorWhite ;

  &:hover {
    color: $accentColor ;
  }
}

  .modal-title {
    color: $DarkThemeColorWhite;
  }

  .modal-img {
    border-color: rgba($color: #ffffff, $alpha: 0.3);
  }

  .modal-values {
    color: $DarkThemeColorWhite;
  }

  .modal-votes {
    color: $DarkThemeColorBlack;
  }

  .modal-overview {
    color: $DarkThemeColorWhite;
  }

  .modal-text {
    color: $DarkThemeColorWhite;
  }

  .modal-btn__watched {
    border-color: $DarkThemeColorWhite;
    color: $DarkThemeColorWhite;
    &:hover {
      border-color: transparent;
    }
  }

  .modal-btn__queue {
    border-color: $DarkThemeColorWhite;
    color: $DarkThemeColorWhite;

        &:hover {
      border-color: transparent;
    }
  }
// End dark style for modal elements

// Films collection style
.films-collection {
  .item__img {
    border-color: rgba($color: #ffffff, $alpha: 0.3);
  }
}

// Pagination dark style
.tui-pagination .tui-page-btn {
  color: $textWhiteColor;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
    background-color: #9d4c15;
}
// End pagination dark style

// Sort button style
.buttons_trending,
.buttons__top_rated,
.buttons__upcoming {
  color: #d7d7d7;
  background-color: #161616;
  border-color: rgba(255, 255, 255, 0.3);
  &:hover,
  &:focus {
    border-color: rgba($color: $accentColor, $alpha: 0.7);
  }
}
// End sort button style

// Modal students style

.backdrop-team {
    background-color: rgba(0, 0, 0, 0.5);
}

.modal-team {
    background: #1f1f1f;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
        0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.team-close-btn {
    background: transparent;
    color: $whiteColor;
    transition: color $animationProps, transform $animationProps;
    &:hover,
    &:focus {
        color: $accentColor;
    }
}

.title-team {
    color: $accentColor;
}

.team-box {
    color: #ffffff;
    transition: transform $animationProps, box-shadow $animationProps;
}

.team-role {
    color: $accentColor;
}
.avatar {
      border: 1px solid rgba($color: $accentColor, $alpha: 0.6)
}
// End modal students style
}


